<template>
  <div>
    <!-- <div
      ref="container"
      style="height: 300px; width: 100%"
      class="monaco-editor"
    ></div> -->
    <codemirror
      ref="codemirror"
      v-model="content"
      :options="options"
    ></codemirror>
  </div>
</template>

<script>
import { codemirror } from 'vue-codemirror'
import '../lib/cm-setting'
export default {
  components: {
    codemirror,
  },
  props: {
    // 
    codes: {
      type: String,
    },
    language: {
      type: String,
      default: 'application/json',
    },
    readonly: {
      type: Boolean,
      default: function () {
        return false
      },
    },
  },
  watch: {
    codes: {
      immediate: true,
      handler(val) {
        if (val) {
          this.content = val
        }
      },
    },
    content: {
      handler(n) {
        this.$emit('change', n)
      },
    },
    readonly: {
      handler(val) {
        console.log('controll', val)
        this.options.readOnly = val
        this.$nextTick(() => {
          this.refreshEditor()
        })
      },
    },
  },
  data() {
    return {
      monacoEditor: null,
      content: '',
      hoverProvider: null,
      options: {
        line: true,
        theme: 'lesser-dark', // 
        tabSize: 4, // 
        indentUnit: 2, //  2
        firstLineNumber: 1, //  1
        readOnly: this.readonly, // 
        autorefresh: true,
        smartIndent: true, // 
        lineNumbers: true, // 
        styleActiveLine: true, // 
        viewportMargin: Infinity, //
        showCursorWhenSelecting: true, // 
        mode: this.language,
      },
    }
  },
  methods: {
    getValue() {
      console.log(this.content)
      return this.content
    },
    // handleVisibilityChange() {
    //   if (document.visibilityState === 'visible') {
    //     this.refreshEditor()
    //   }
    // },
    // handleFocus() {
    //   // 
    //   if (this.$refs.codemirror) {
    //     this.refreshEditor()
    //   }
    // },
    refreshEditor() {
      this.$nextTick(() => {
        if (this.$refs.codemirror) {
          this.$refs.codemirror.codemirror.focus()
          this.$refs.codemirror.codemirror.refresh()
        }
      })
    },
  },
  // mounted() {
  //   // window.addEventListener('focus', this.handleFocus)
  //   // document.addEventListener('visibilitychange', this.handleVisibilityChange)
  // },
  // beforeDestroy() {
  //   // 
  //   window.removeEventListener('focus', this.handleFocus)
  //   document.removeEventListener(
  //     'visibilitychange',
  //     this.handleVisibilityChange
  //   )
  // },
}
</script>

